.globalErrorBlock {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    max-width: 500px;
    position: fixed;
    top: 0;
    left: 50%;
    background-color: #cecdcd;
    border: 2px solid gray;
    border-radius: 10px;
    z-index: 999;
}

.globalError {
    padding: 10px;
    font-size: 20px;
    color: #f60404;
}