.loginForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.inputWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.input {
    border-radius: 5px;
    border: 1px solid transparent;
    background-color: #dedede;
    margin: 5px 0;
}

.error {
    color: red;

}

.buttonWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
    margin-top: 5px;
    border: 2px solid #dedede;
    border-radius: 5px;
    background-color: #00ccff;
    color: #FFFFFF;
    font-size: 14px;
    cursor: pointer;
}