.nav {
  grid-area: nav;
  background-color: #00ccff;
  border-radius: 10px;
  min-height: 230px;

}

.item a {
  color: #fff;
  text-decoration: none;
}

.item a.active {
  color: #f60404;
  margin-left: 10px;

}

.item {
  font-size: 20px;
  margin-left: 10px;
  margin-top: 10px;
}

.friends {
  display: flex;
  flex-direction: row;
  padding-left: 5px;
}

.friendsAvatar {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50px;
}
.friendsAvatar img{
  width: 30px;
  padding: 5px 5px 0 0;
}


