.container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
}

.avatar img {
    max-width: 100px;
    max-height: 100px;
}

.usersWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 200px;
    margin: 10px 5px;
}

.button {
    background-color: rgb(89, 158, 236);
    border-radius: 5px;
    margin: 5px 0;

}
