.header {
    grid-area: header;
    background-color: #00ccff;
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
}
.logoBlock {
    display: flex;
    align-items: center;
    padding-left: 10px;
}
.logo {
    margin-left: 10px;
}


.loginBlock {
    display: flex;
    flex-direction: row-reverse;
    align-content: space-between;
}
.login {
    padding-right: 10px;
    align-self: center;
    color: #FFFFFF;
    text-decoration: none;
}

.avatar{
    width: 50px;
    align-self: center;

}
.button {
    border: 0 solid transparent;
    margin-left: 5px;
    background-color: #00ccff;
    color: #FFFFFF;
    font-size: 14px;
    cursor: pointer;
}