.appWrapper {
  background-color: transparent;
  display: grid;
  max-width: 1200px;
  margin: 0 auto;
  grid-template-areas:
  "header header"
  "nav content"
  "footer footer";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 1.6fr 10fr;
  grid-gap: 10px;
}

.appWrapperContent {
  grid-area: content;
  background-color: #00ccff;
  min-height: 500px;
  padding: 10px;
  border-radius: 10px;
}







